html{
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

#root{
  height: 100%;
}

::-webkit-scrollbar{
  width: 10px;
}

::-webkit-scrollbar-thumb{
  background-color: #777;
  border-radius: 10px;
}
html{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  height: 100%;
}

.container{
  height: 100%;
  text-align: center;
  color: #ddd;
  font-family: 'PT Sans';
  box-sizing: border-box;
  overflow: hidden;
}

.content{
  height: 100%;
  background: -webkit-linear-gradient(#444, #000);
  background: linear-gradient(#444, #000);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.comps{
  -webkit-flex: 1 1 100%;
          flex: 1 1 100%;
  overflow: hidden;
}

.h100, .react-swipeable-view-container{
  height: 100%;
}

header{
    margin-bottom: 5vh;
    text-align: center;
    margin: 0;
}

header ul{
    list-style: none;
    margin: 0 2vw;
    padding: 0;
}

header li{
    display: inline-block;
    width: calc(100% / 2);
}

header a{
    color: #bbb;
    text-decoration: none;
    font-size: 12pt;
    display: block;
    padding: 10px;
    box-sizing: border-box;
    padding: 2vh 0;
    outline: none;
}

header .active{
    color: white;
    border-bottom: 2px solid #ddd;
}
